import type { AppEvent } from "./types";
import { parseIcs } from "./parseIcs";
import { keepOnlyDeadlines } from "./ics/filterDeadlines";

const PROXY = (import.meta.env.VITE_ICS_PROXY_URL || "").trim();
const KEY = (import.meta.env.VITE_ICS_KEY || "").trim();

/**
 * Fetch ICS calendar via API proxy
 *
 * Example Moodle URL:
 * https://lms.astanait.edu.kz/calendar/export_execute.php?userid=18424&authtoken=...&preset_what=all&preset_time=recentupcoming
 */
export async function fetchCalendarIcs(icsUrl: string): Promise<AppEvent[]> {
  const url = (icsUrl || "").trim();
  if (!url) return [];

  if (!PROXY) throw new Error("ICS proxy is not configured (VITE_ICS_PROXY_URL).");
  if (!KEY) throw new Error("ICS key is not configured (VITE_ICS_KEY).");
  if (import.meta.env.DEV) {
    console.log("[ICS] Using proxy:", PROXY);
    console.log("[ICS] Key length:", KEY.length);
  }

  const base = PROXY.replace(/\/$/, "");
  const reqUrl = base.endsWith("/ics")
    ? `${base}?url=${encodeURIComponent(url)}`
    : `${base}/ics?url=${encodeURIComponent(url)}`;
  const response = await fetch(reqUrl, {
    headers: {
      "X-ICS-KEY": KEY,
      "Accept": "text/calendar,*/*"
    }
  });

  const icsText = await response.text();

  if (!response.ok) {
    throw new Error(`ICS proxy error ${response.status}: ${icsText}`);
  }

  if (!icsText || !icsText.trim().startsWith("BEGIN:VCALENDAR")) {
    throw new Error("Invalid ICS format received from server");
  }

  const events = parseIcs(icsText);
  return keepOnlyDeadlines(events);
}
