import { useMemo, useState } from "react";
import type { AppEvent } from "../calendar/types";
import { normalizeCourseKey } from "../../shared/utils/normalizeCourse";
import { useSettings } from "./useSettings";
import { useT } from "../i18n/useT";

function collectCourses(events: AppEvent[]) {
  const map = new Map<string, string>(); // key -> name
  for (const e of events) {
    const name = (e.courseName || e.title || "").trim();
    if (!name) continue;
    const key = e.courseKey || normalizeCourseKey(name);
    if (!key) continue;
    if (!map.has(key)) map.set(key, name);
  }
  return Array.from(map.entries())
    .map(([key, name]) => ({ key, name }))
    .sort((a, b) => a.name.localeCompare(b.name));
}

export function HiddenSubjectsSheet({
  open,
  onClose,
  events,
  inline,
}: {
  open: boolean;
  onClose: () => void;
  events: AppEvent[];
  inline?: boolean;
}) {
  const t = useT();
  const [s, setS] = useSettings();
  const [q, setQ] = useState("");

  const all = useMemo(() => collectCourses(events), [events]);
  const hidden = useMemo(() => new Set(s.hiddenCourses || []), [s.hiddenCourses]);
  const hasHidden = hidden.size > 0;

  const filtered = useMemo(() => {
    const qq = q.trim().toLowerCase();
    if (!qq) return all;
    return all.filter((x) => x.name.toLowerCase().includes(qq));
  }, [all, q]);

  const toggle = (key: string) => {
    const cur = new Set(s.hiddenCourses || []);
    if (cur.has(key)) cur.delete(key);
    else cur.add(key);
    setS({ hiddenCourses: Array.from(cur) });
  };

  if (!open) return null;

  const content = (
    <>
      <div className="sheet-head">
        <div>
          <div className="sheet-title">{t("settings.subjects")}</div>
          <div className="muted small">{t("settings.manageHidden")}</div>
        </div>
        <button className="btn ghost" onClick={onClose} aria-label={t("common.close")}>
          x
        </button>
      </div>

      <div className="sheet-body">
        <div className="sheet-tools">
          <input className="input" value={q} onChange={(e) => setQ(e.target.value)} placeholder={t("settings.subjects.search")} />
          <button className="btn ghost sheet-action-btn" onClick={() => setS({ hiddenCourses: [] })} disabled={!hasHidden}>
            {t("settings.subjects.restoreAll")}
          </button>
        </div>

        <div className="sheet-list">
          {filtered.map((x) => {
            const isH = hidden.has(x.key);
            return (
              <div key={x.key} className={`sheet-item ${isH ? "is-hidden" : ""}`}>
                <div className="left">
                  <div className="name">{x.name}</div>
                  <div className="sheet-meta muted small">
                    <span>{x.key}</span>
                    <span className={`sheet-status ${isH ? "is-hidden" : "is-shown"}`}>
                      {isH ? t("settings.subjects.hidden") : t("settings.subjects.shown")}
                    </span>
                  </div>
                </div>
                <div className="right">
                  <button className={`btn ghost sheet-toggle ${isH ? "is-show" : "is-hide"}`} onClick={() => toggle(x.key)}>
                    {isH ? t("settings.subjects.restore") : t("settings.subjects.hide")}
                  </button>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );

  if (inline) {
    return (
      <div className="sheet inline">
        {content}
      </div>
    );
  }

  return (
    <div className="sheet-backdrop is-modal" role="dialog" aria-modal="true" onClick={onClose}>
      <div className="sheet is-modal" onClick={(e) => e.stopPropagation()}>
        {content}
      </div>
    </div>
  );
}
