import { setPendingRoom } from "./roomFocus";
import { navigate } from "../../app/routes";

/**
 * Open map page and focus on a room
 */
export function openMapWithRoom(room: string): void {
  if (!room?.trim()) return;
  setPendingRoom(room.trim());
  navigate("/map");
}

/**
 * Create a click handler that opens map with room focus
 */
export function createRoomLinkHandler(room?: string): (() => void) | undefined {
  if (!room?.trim()) return undefined;
  return () => openMapWithRoom(room);
}
