import { useEffect } from "react";
import { useEvents } from "../calendar/useEvents";
import { useSettings } from "../settings/useSettings";
import { tickNotifications } from "./scheduler";

export function useNotificationScheduler() {
  const events = useEvents();
  const [settings] = useSettings();

  useEffect(() => {
    // small delay to avoid firing on cold-start jitter
    const t0 = window.setTimeout(() => tickNotifications(events, settings), 1200);

    const id = window.setInterval(() => {
      tickNotifications(events, settings);
    }, 30_000);

    return () => {
      window.clearTimeout(t0);
      window.clearInterval(id);
    };
  }, [events, settings]);
}
