/**
 * DU schedule is proxied by Cloudflare Worker:
 *   GET /du/schedule?group=GROUP_NAME
 *
 * Requires header:
 *   X-ICS-KEY: <secret>
 */

import { normalizeDuLessons } from "./duNormalize";

const DU_PROXY_BASE = ((import.meta as any).env.VITE_DU_PROXY_URL || "").trim();
const ICS_KEY = ((import.meta as any).env.VITE_ICS_KEY || "").trim();

function normalizeGroup(input: string) {
  return (input || "")
    .trim()
    .replace(/[-]/g, "-"); // normalize dash variants
}

async function readTextSafe(r: Response) {
  try {
    return await r.text();
  } catch {
    return "";
  }
}

export async function fetchDuSchedule(group: string) {
  const g = normalizeGroup(group);
  if (!g) throw new Error("group is empty");

  if (!DU_PROXY_BASE) {
    throw new Error("VITE_DU_PROXY_URL is missing. Set it in Cloudflare Pages env.");
  }
  if (!ICS_KEY) {
    throw new Error("VITE_ICS_KEY is missing. Set it in Cloudflare Pages env.");
  }

  const url = `${DU_PROXY_BASE}?group=${encodeURIComponent(g)}`;

  const r = await fetch(url, {
    method: "GET",
    headers: {
      "X-ICS-KEY": ICS_KEY,
      "Accept": "application/json",
    },
  });

  const text = await readTextSafe(r);

  if (!r.ok) {
    throw new Error(`DU proxy error ${r.status}: ${text}`);
  }

  //    HTML  
  if (text.trim().startsWith("<")) {
    throw new Error(`DU proxy returned HTML, not JSON: ${text.slice(0, 120)}`);
  }


  try {
    const json = JSON.parse(text);
    return normalizeDuLessons(json); //   
  } catch {
    throw new Error(`DU proxy returned invalid JSON: ${text.slice(0, 200)}`);
  }
}
