import { useEffect, useMemo, useState, type FC } from "react";
import { useT } from "../../features/i18n/useT";

type Route = "/" | "/deadlines" | "/schedule" | "/map" | "/settings";

function go(path: Route) {
  if (window.location.pathname === path) return;
  window.history.pushState({}, "", path);
  window.dispatchEvent(new Event("app:navigate"));
}

function usePath() {
  const [p, setP] = useState<Route>(() => {
    const path = window.location.pathname;
    if (path === "/" || path === "") return "/";
    if (path === "/schedule") return "/schedule";
    if (path === "/deadlines") return "/deadlines";
    if (path === "/map") return "/map";
    if (path === "/settings") return "/settings";
    return "/";
  });

  useEffect(() => {
    const on = () => {
      const path = window.location.pathname;
      let newPath: Route = "/";
      if (path === "/" || path === "") newPath = "/";
      else if (path === "/schedule") newPath = "/schedule";
      else if (path === "/deadlines") newPath = "/deadlines";
      else if (path === "/map") newPath = "/map";
      else if (path === "/settings") newPath = "/settings";
      setP(newPath);
    };
    window.addEventListener("popstate", on);
    window.addEventListener("app:navigate", on);
    return () => {
      window.removeEventListener("popstate", on);
      window.removeEventListener("app:navigate", on);
    };
  }, []);

  return p;
}

function isActive(path: string, route: Route) {
  if (route === "/") return path === "/";
  return path === route;
}

function IcHome() {
  return (
    <svg viewBox="0 0 24 24" aria-hidden="true">
      <path
        d="M3 10.5 12 3l9 7.5V21a1 1 0 0 1-1 1h-5v-7H9v7H4a1 1 0 0 1-1-1z"
        fill="currentColor"
      />
    </svg>
  );
}

