export function BottomNav() {
  const t = useT();
  const path = usePath();

  const items: Array<{
    route: Route;
    label: string;
    Icon: FC;
  }> = useMemo(
    () => [
      { route: "/", label: t("nav.home"), Icon: IcHome },
      { route: "/deadlines", label: t("nav.deadlines"), Icon: IcDeadlines },
      { route: "/schedule", label: t("nav.schedule"), Icon: IcSchedule },
      { route: "/map", label: t("nav.map"), Icon: IcMap },
      { route: "/settings", label: t("nav.settings"), Icon: IcSettings }
    ],
    [t]
  );

  return (
    <nav className="bottom-nav" aria-label={t("common.navigation")}>
      <div className="bottom-nav-inner">
        {items.map(({ route, label, Icon }) => {
          const active = isActive(path, route);
          return (
            <button
              key={route}
              className={`nav-item ${active ? "is-active" : ""}`}
              onClick={() => go(route)}
              aria-label={label}
            >
              <span className="nav-ic" aria-hidden="true">
                <Icon />
              </span>
              <span className="nav-dot" aria-hidden="true" />
            </button>
          );
        })}
      </div>
    </nav>
  );
} 
