import { useEffect, useMemo, useRef, useState } from "react";
import type { AppEvent } from "./types";
import { useSettings } from "../settings/useSettings";
import { normalizeCourseKey } from "../../shared/utils/normalizeCourse";
import { fetchCalendarIcs } from "./api";
import { filterHiddenCourses } from "./selectors";
import { loadGroupScheduleAsEvents } from "./group/groupSource";
import { useToasts } from "../../shared/components/Toast";
import { useT } from "../i18n/useT";

function sortEvents(a: AppEvent, b: AppEvent) {
  const ta = new Date(a.due || a.start).getTime();
  const tb = new Date(b.due || b.start).getTime();
  return ta - tb;
}

let lastStatusGlobal: "ok" | "error" | null = null;
let lastToastAt = 0;

export function useEvents(): AppEvent[] {
  const [settings] = useSettings();
  const [raw, setRaw] = useState<AppEvent[]>([]);
  const timerRef = useRef<number | null>(null);
  const { show } = useToasts();
  const t = useT();

  useEffect(() => {
    let alive = true;

    async function run() {
      const isIcsOnly = settings.scheduleSource === "ics";
      const icsUrl = (settings.icsUrl || "").trim();
      const groupName = (settings.selectedGroup || "").trim();
      let didFetch = false;

      try {
        let icsEvents: AppEvent[] = [];
        let groupEvents: AppEvent[] = [];

        if (icsUrl) {
          try {
            icsEvents = await fetchCalendarIcs(icsUrl);
            didFetch = true;
          } catch (e) {
            console.warn("useEvents ICS load failed", e);
          }
        } else if (isIcsOnly) {
          if (alive) setRaw([]);
          return;
        }

        if (!isIcsOnly) {
          if (!groupName) {
            if (alive && !icsEvents.length) setRaw([]);
            return;
          }

          try {
            groupEvents = await loadGroupScheduleAsEvents(groupName);
            didFetch = true;
          } catch (e) {
            console.warn("useEvents GROUP load failed", e);
          }
        }

        let merged: AppEvent[] = [];
        if (isIcsOnly) {
          merged = icsEvents;
        } else {
          const deadlines = icsEvents.filter((e) => e.kind === "deadline" || e.kind === "assignment");
          const lessons = groupEvents.filter((e) => e.kind === "lesson");
          merged = [...lessons, ...deadlines];
        }

        if (alive && didFetch) setRaw(merged);

        if (icsUrl && didFetch && lastStatusGlobal !== "ok") {
          const now = Date.now();
          if (now - lastToastAt > 3000) {
            lastStatusGlobal = "ok";
            lastToastAt = now;
            show(t("common.connection.ok"), undefined, 5000, "success");
          }
        }
      } catch (e) {
        console.warn("useEvents load failed", e);

        const now = Date.now();
        if (icsUrl && lastStatusGlobal !== "error" && now - lastToastAt > 3000) {
          lastStatusGlobal = "error";
          lastToastAt = now;
          show(t("common.connection.fail"), undefined, 5000, "danger");
        }
      }
    }

    run();

    if (timerRef.current) window.clearInterval(timerRef.current);

    const intervalMs = Math.max(1, settings.autoRefreshMinutes || 30) * 60_000;
    timerRef.current = window.setInterval(() => run(), intervalMs);

    return () => {
      alive = false;
      if (timerRef.current) window.clearInterval(timerRef.current);
      timerRef.current = null;
    };
  }, [
    settings.scheduleSource,
    settings.icsUrl,
    settings.selectedGroup,
    settings.autoRefreshMinutes,
  ]);

  const filtered = useMemo(() => {
    return filterHiddenCourses(raw, settings.hiddenCourses || [], normalizeCourseKey)
      .slice()
      .sort(sortEvents);
  }, [raw, settings.hiddenCourses]);

  return filtered;
}
